body{
  display: flex;
  justify-content: center;
  align-items:center;
  background-color: black;
}
.container {
  height: 600px;
  width: 600px;
  background-color: rgb(0, 250, 0,0.5);
  border: 30px solid balck;
  border-radius: 20px;
  box-shadow: 10px 10px 10px rgb(0, 0, 245,0.7);
  margin-top: 20px;
}
.displays {
  height: 50px;
  width: 90%;
  background-color:rgb(255, 255, 255,0.7);
  margin: 30px;
  position: relative;
  top: 3%;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: large;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
  height: 100px;
}
#btn1{
  width: 400px;
  height: 40px;
  border-radius: 20px;
  background-color: rgb(20,20,235,0.7);
}
.enterCaptcha{
   position: relative;
   left: 25%;
   top: 5%;
}
#ip {
  height: 30px;
  width: 50%;
}
.btns {
  position: relative;
  top: 15%;
  left: 25%;
  height: 75px;
}
.sub,.res {
  margin: 20px;
  width: 90px;
}
#note {
  font-style: italic;
  color: rgb(255, 0, 0);
  font-size: larger;
  text-shadow: 3px 3px 3px rgb(0, 0, 0);
  font-weight: bolder;
}